// Import Styled
import styled from 'styled-components';

export const BlockDefault = styled.div`
	.map{
		@media (min-width: 992px) {
	    	width: 33.3%;	
		}
	}
	.name{
		display: inline-block;
		margin-left: 12px;
    	vertical-align: top;
    	margin-bottom: 24px;
	}
	.number{
		text{
			float: right;
		}
	}	
	.tag{
		text{
			float: right;
		}
	}
	.tags{
		text{
			margin-right: 12px;
		}
		> div{
			display: flex;
		}
		.number{
			text{    
				position: absolute;
    			right: 0;
    			margin-right: 0;
			}
		}
	}
`;